<template>
  <div class="wrap">
    <div class="label">请输入昵称</div>
    <div>
      <van-field v-model="text" />
    </div>
    <van-button color="#FF9900" size="small" type="info" @click="login">
      登录
    </van-button>
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {
  data() {
    return {
      text: '',
    };
  },
  created() {
    const nickname = localStorage.getItem('nickname');
    if (nickname) {
      this.$router.push('/home');
    }
  },
  methods: {
    login() {
      if (!this.text) {
        return Toast('请输入昵称');
      }
      localStorage.setItem('nickname', this.text);
      this.$router.push('/home');
    },
  },
};
</script>

<style lang="less" scoped>
.wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.label {
  margin-bottom: 20px;
}

.van-field {
  border: 1px solid #cdcdcd;
  text-align: center;
}

.van-button {
  width: 200px;
  height: 46px;
}
</style>
